<template>
	<view class="song-list-comp">
		<view class="tit-bar" v-if="showTitle">
			{{title}}
			<text @click="toAlbum" class="more fr">更多歌单</text>
		</view>
		<view class="clearfix" v-if="!scrollX">
			<view class="item" v-for="(item, index) in list" :key="index" @click="toDetail(item)">
				<image class="img" :src="item.coverImgUrl"></image>
				<view class="desc">{{item.name}}</view>
				<view class="count">{{item.playCount>10000?(Math.round(item.playCount/1000)/10+'万'):item.playCount}}</view>
			</view>
		</view>
		<view class="scroll-box"  v-if="scrollX">
			<scroll-view scroll-x class="box">
				<view class="item" v-for="(item, index) in list" :key="index" @click="toDetail(item)">
					<image class="img" :src="item.coverImgUrl"></image>
					<view class="desc">{{item.name}}</view>
					<view class="count">{{item.playCount>10000?(Math.round(item.playCount/1000)/10+'万'):item.playCount}}</view>
				</view>
			</scroll-view>
		</view>

	</view>
</template>

<script>
	export default {
		// 从父组件传值的 共有的有主标题，链接更多，歌单数据
		props: {
			title: {
				type: String,
				default: ''
			},
			link: {
				type: String,
				default: ''
			},
			list: {
				type: Array,
				default () {
					return []
				}
			},
			scrollX: {
				type: Boolean,
				default: false
			},
			showTitle:{
				type: Boolean,
				default: true
			}
		},
		data() {
			return {}
		},
		methods: {
			toDetail(item){
				uni.navigateTo({
					animationDuration:500,
					animationType:'pop-in',
					url:'/pages/index/musicalbum?id='+item.id
				})
			},
			toAlbum(){
				uni.switchTab({
					url:'/pages/music/index'
				})
			}
		}

	}
</script>
<style lang="scss">
	.song-list-comp {
		padding-left: 32rpx;
		margin-top: 10rpx;

		.tit-bar {
			font-size: 34rpx;
			font-weight: 600;
			line-height: 110rpx;
		}

		.more {
			width: 150rpx;
			height: 50rpx;
			line-height: 50rpx;
			text-align: center;
			margin-top: 30rpx;
			margin-right: 32rpx;
			font-weight: 400;
			font-size: 24rpx;
			border: 2rpx solid #e6e6e6;
			border-radius: 50rpx;
		}
		.scroll-box{
			height: 320rpx;
			.box{
				height: 320rpx;
					width: 100%;
					white-space: nowrap;
			}
			.item{
				float: unset;
				display: inline-block;
			}
			.desc{
				white-space: pre-wrap;
			}
		}
		.item {
			float: left;
			position: relative;
			width: 216rpx;
			padding-bottom: 20rpx;
			margin-right: 16rpx;
			line-height: 34rpx;

			&.video {
				width: 450rpx;
			}

			.img {
				display: block;
				width: 100%;
				height: 216rpx;
				margin-bottom: 16rpx;
				background: #eee;
				border-radius: 10rpx;
			}

			.desc {
				height: 64rpx;
				margin-top: 12rpx;
				line-height: 32rpx;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}

			.count {
				position: absolute;
				top: 0;
				right: 0;
				padding-left: 25rpx;
				line-height: 34rpx;
				color: #fff;
				font-size: 24rpx;
				z-index: 10;
				background: url(data:image/png;base64,) no-repeat;
				background-size: 25rpx 25rpx;
				transform: scale(0.8);
			}
		}
	}
</style>
